import { useState, useEffect } from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { Toaster } from 'react-hot-toast';
import Header from './components/Header';
import ProductInput from './components/ProductInput';
import ProductInfo from './components/ProductInfo';
import PriceTrendGraph from './components/PriceTrendGraph';
import ComparisonTable from './components/ComparisonTable';
import AlertConfirmation from './components/AlertConfirmation';
import Footer from './components/Footer';
import ErrorBoundary from './components/ErrorBoundary';
import LoadingSpinner from './components/LoadingSpinner';

// API endpoint
const API_BASE_URL = import.meta.env.VITE_API_URL || 'http://localhost:5000';

interface ProductData {
  url: string;
  targetPrice?: number;
  email?: string;
}

interface ProductDetails {
  image: string;
  title: string;
  currentPrice: number;
  amazonUrl: string;
  status: 'tracking' | 'alert_set' | 'price_drop';
  priceHistory: Array<{ date: string; price: number }>;
  platformPrices: Array<{
    platform: string;
    title: string;
    price: number;
    url: string;
  }>;
}

function App() {
  const [productData, setProductData] = useState<ProductData | null>(null);
  const [productDetails, setProductDetails] = useState<ProductDetails | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isRefreshing, setIsRefreshing] = useState(false);

  // Load saved product data from localStorage on mount
  useEffect(() => {
    const savedProductData = localStorage.getItem('productData');
    if (savedProductData) {
      try {
        const parsedData = JSON.parse(savedProductData);
        setProductData(parsedData);
        handleRefreshData(parsedData.url);
      } catch (err) {
        console.error('Error loading saved product data:', err);
        localStorage.removeItem('productData');
      }
    }
  }, []);

  const handleProductSubmit = async (data: ProductData) => {
    try {
      setIsLoading(true);
      setError(null);

      // First, submit the product URL
      const response = await fetch(`${API_BASE_URL}/api/track`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to start tracking product');
      }

      const result = await response.json();
      setProductData(data);
      setProductDetails(result);
      
      // Save to localStorage
      localStorage.setItem('productData', JSON.stringify(data));
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
      throw err; // Re-throw to be handled by the component
    } finally {
      setIsLoading(false);
    }
  };

  const handleRefreshData = async (url: string) => {
    try {
      setIsRefreshing(true);
      setError(null);

      const response = await fetch(`${API_BASE_URL}/api/refresh/${encodeURIComponent(url)}`);
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.message || 'Failed to refresh data');
      }

      const result = await response.json();
      setProductDetails(result);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'An error occurred');
    } finally {
      setIsRefreshing(false);
    }
  };

  const handleClearData = () => {
    setProductData(null);
    setProductDetails(null);
    localStorage.removeItem('productData');
  };

  return (
    <ErrorBoundary>
      <Router>
        <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
          <Header />
          <main className="container mx-auto px-4 py-8 space-y-8">
            {!productDetails && (
              <ProductInput onSubmit={handleProductSubmit} />
            )}
            
            {isLoading && !productDetails && (
              <div className="flex justify-center items-center py-12">
                <LoadingSpinner size="lg" text="Loading product data..." />
              </div>
            )}

            {error && (
              <div className="bg-red-50 dark:bg-red-900/50 border border-red-200 dark:border-red-800 rounded-lg p-4 text-red-600 dark:text-red-400">
                <div className="flex items-center">
                  <svg
                    className="h-5 w-5 mr-2"
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z"
                    />
                  </svg>
                  {error}
                </div>
              </div>
            )}
            
            {productDetails && (
              <>
                <div className="flex justify-end">
                  <button
                    onClick={handleClearData}
                    className="text-sm text-gray-500 hover:text-gray-700 dark:text-gray-400 dark:hover:text-gray-300"
                  >
                    Clear Data
                  </button>
                </div>
                
                <ProductInfo
                  image={productDetails.image}
                  title={productDetails.title}
                  currentPrice={productDetails.currentPrice}
                  amazonUrl={productDetails.amazonUrl}
                  status={productDetails.status}
                />
                
                <div className="relative">
                  {isRefreshing && (
                    <div className="absolute inset-0 bg-white/50 dark:bg-gray-800/50 flex items-center justify-center z-10">
                      <LoadingSpinner size="md" text="Refreshing data..." />
                    </div>
                  )}
                  <PriceTrendGraph
                    data={productDetails.priceHistory}
                    onRefresh={() => productData?.url && handleRefreshData(productData.url)}
                  />
                </div>
                
                <ComparisonTable prices={productDetails.platformPrices} />
                
                <AlertConfirmation
                  targetPrice={productData?.targetPrice}
                  email={productData?.email}
                  isPriceDrop={productDetails.status === 'price_drop'}
                />
              </>
            )}
          </main>
          <Footer />
          <Toaster
            position="bottom-right"
            toastOptions={{
              duration: 4000,
              style: {
                background: '#333',
                color: '#fff',
              },
            }}
          />
        </div>
      </Router>
    </ErrorBoundary>
  );
}

export default App;
